
.container--faq {
    padding-top: 60px;
    padding-bottom: 60px;

    @media screen and (min-width: $tablet) {
        padding-top: 80px;
        padding-bottom: 80px;
    }

    @media screen and (min-width: $desktop) {
        padding-top: 100px;
        padding-bottom: 100px;
    }
}

.faq-wraper {
    display: flex;
    flex-direction: column;

    @media screen and (min-width: $desktop) {
        flex-direction: row;
        justify-content: space-between;
    }
}

.section-title--faq {
    color: $white-primary;
    font-size: $font-size-seventh;

    @media screen and (min-width: $tablet) {
        font-size: $font-size-fourth;
    }
}

.section-title-word-faq {
    color: $white-primary-rgb;
}

.faq-text {
    margin-top: 40px;
    color: $white-primary-rgb;
    font-weight: $regular;
    line-height: $secondary2;

    @media screen and (min-width: $desktop) {
        width: 411px;
    }
}

.faq-list {
    display: flex;
    flex-direction: column;
    margin-top: 40px;
    row-gap: 24px;

    @media screen and (min-width: $desktop) {
        width: 628px;
    }
}

.faq-item {
    display: flex;

    padding-bottom: 12px;
    align-items: center;
    border-bottom: 1px solid $grey-primary-rgb;
}

.faq-item-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.faq-item__title {
    color: $white-primary;
    font-weight: $medium-second;
}

.faq-item__text {
    display: none;
    margin-top: 12px;
    color: $white-primary-rgb;
    font-weight: $regular;
    line-height: $secondary2;

}

.faq-icon {
    color: $white-rgb;
    transition: color $transition-duration;
    &:hover {
        color: $accent;
    }
    cursor: pointer;
}
